.card{
	position: relative;
	padding: 1.2rem;
	
	border-radius: 0.2rem;
	
	-webkit-box-shadow: var(--shadow);
	
	box-shadow: var(--shadow);
	
	overflow: hidden;
	
	margin-bottom: 1.2rem;
}

@media screen and (min-width: 768px ) {
	.card {
		margin-bottom: 4.8rem;
	}
}

.card h1{
	color: var(--black87);
	position: relative;
	display: inline-block;

	-webkit-transition: color 200ms ease;

	transition: color 200ms ease;
}
.card p{
	color: var(--black87);
	font-size: 1.4rem;
	max-width: 45rem;
	padding-bottom: 1.2rem;
}

.card:hover h1{
	color: var(--primary);
}

@media screen and (min-width: 480px ) {
	.card {
		padding: 2.4rem;
	}
	.card p {
		font-size: 1.6rem;
	}
}

.card:after{
	position: absolute;
	z-index: -1;
	content:"";
	width: 100%;
	height: 100%;
	
	left:0;
	top:0;
	
	background-image: linear-gradient(45deg, #00B3FF, #75DFC0 40%, #75DFC0 60%, #00B9B6);
	-webkit-background-image: linear-gradient(45deg, #00B3FF, #75DFC0 40%, #75DFC0 60%, #00B9B6);
	
	opacity: 0.5;
	
	background-size: 100% 100%;
	
	-webkit-transition: -webkit-transform 300ms ease;
	
	transition: -webkit-transform 300ms ease;
	
	transition: transform 300ms ease;
	
	transition: transform 300ms ease, -webkit-transform 300ms ease;
}

a:nth-child(3) .card:after{
	background-image: linear-gradient(45deg, #F5564E, #F0DFD9 40%, #F0DFD9 60%, #FFD166);
	-webkit-background-image: linear-gradient(45deg, #F5564E, #F0DFD9 40%, #F0DFD9 60%, #FFD166);
}

a:nth-child(4) .card:after{
	background-image: linear-gradient(45deg, #0D5AFF, #C5CCFF 40%, #C5CCFF 50%, #83ECEB);
	-webkit-background-image: linear-gradient(45deg, #0D5AFF, #C5CCFF 40%, #C5CCFF 50%, #83ECEB);
}

.card:hover:after{
	-webkit-transform: scale(1.6);
	transform: scale(1.6);
}

.pic{
	-ms-flex-item-align: end;
	align-self: flex-end;
	
	height: auto;
	width: 100%;
	
	-webkit-box-flex: 1;
	
	-ms-flex-positive: 1;
	
	flex-grow: 1;
	
	-o-object-fit: contain;
	
	object-fit: contain;
	-o-object-position: 50% 50%;
	object-position: 50% 50%;
}



