.pageWrapper {
	height: 100%;

	display: -webkit-box;

	display: -ms-flexbox;

	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}

.errorType {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	position: relative;
	padding: 4.8rem 0;
}
.errorType__content{
	padding: 1.2rem;
	margin: auto;

	border-radius: 0.8rem;
	-webkit-box-shadow: var(--shadow);
	        box-shadow: var(--shadow);
	
	background-color: #FFF;
}

.errorType__content a{
	padding-top:1.2rem;
	display: block;

	text-align: right;

	font-weight: 400;
	font-family: 'Work Sans', sans-serif;
	font-size: 1.4rem;
	color: var(--primary);
}

.errorType__content i{
	font-size: 1rem;
	padding-right:0.6rem;
}

.errorType h1{
	color: var(--accent);
}




