.wrapper{
	padding-top: 0;

	display: -webkit-box;

	display: -ms-flexbox;

	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;

	padding-left: 1.2rem;
	padding-right: 1.2rem;
	padding-top: 1.2rem;
}

@media screen and (min-width: 768px ) {
	.wrapper {
		padding-top: 4.8rem;
	}
}

.cards{
	width: 100%;
	max-width: var(--tablet);
	margin: auto;
}

.cards a{
	text-decoration: none;
}

.title{
	padding-top: 1.2rem;
	padding-bottom: 0.6rem;
	padding-left: 0.6rem;
	color: var(--black60);
}

@media screen and (min-width: 768px ) {
	.title {
		padding-top: 4.8rem;
		padding-bottom: 1.2rem;
	}
	.cards{
		padding: 0;
	}
}

.home {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: start;
	    -ms-flex-align: start;
	        align-items: flex-start;

	width: 100%;
	height: 64rem;

	margin: auto;
	padding: 2.4rem;
	
	-webkit-box-shadow: var(--shadow);
	
	        box-shadow: var(--shadow);

	background: url(../../assets/illustration.svg);
	background-position: 50% 10%;
	background-repeat: no-repeat;
	background-size: cover;

	overflow: hidden;
	border-radius: 0 0 0.2rem 0.2rem;
}

.home__content{
	padding: 2.4rem;

	background: #FFF;

	border-radius: 0.2rem;
	-webkit-box-shadow: var(--shadow);
	        box-shadow: var(--shadow);
}

 @media screen and (min-width: 768px ) {
	.home {
		-webkit-box-pack: left;
		    -ms-flex-pack: left;
		        justify-content: left;

		border-radius: 0.2rem;
		height: 48rem;
		width: var(--tablet);

		background-size: 120%;
		background-position: 20% 60%;
	}

}

.home__content h1 {
	font-size: 2.4rem;
	line-height: 2.6rem;
	font-weight: 700;
	font-family: 'Quicksand', sans-serif;
	color: #000;

	letter-spacing: 0.1em;
	
	text-decoration: none;
}

.home__content h2 {
	padding-top: 1.2rem;

	font-weight: 400;
	font-family: 'Work Sans', sans-serif;
	font-size: 1.8rem;
	color: #000;
	line-height: 2.4rem;
	
	text-decoration: none;
}

.home__content span {
	color: var(--accent);
}

.home__content .email {
	padding-top:1.2rem;
	display: block;

	text-align: right;

	font-weight: 400;
	font-family: 'Work Sans', sans-serif;
	font-size: 1.4rem;
	color: var(--primary);
}


