
.projectWrapper {
    max-width: var(--tablet);
    margin: 0 auto;
}

.note{
    float: left;
    font-size: 1.4rem;
    padding-right: 2.4rem;
    padding-left: 1.2rem;
}
.note span{
    font-weight: 600;
}

.projectPart{
    padding: 1.2rem;
    padding-bottom: 2.4rem;
    clear: both;
}

.projectWrapper h1{
    font-size: 2.4rem;
    letter-spacing: 0.1em;
    
    text-transform: uppercase;
    
    padding-bottom: 1.2rem;
}

.projectWrapper h2{
    color: var(--black38);
    
    font-size: 1.8rem;
    letter-spacing: 0.03em;
    
    text-transform: uppercase;
}


.projectPart__details{
    max-width: 50rem;
}

.picContainer{
    padding-top: 1.2rem;
    display: flex;
    flex-direction: column;
} 

.pic{ 
    margin: auto;
    padding: 1.2rem;
}

.pic img{
    max-width: 100%;
    height: auto;
    -o-object-fit: contain;
    object-fit: contain;
    
    -webkit-box-shadow: var(--shadow);
    box-shadow: var(--shadow); 
    
    max-height: 36rem;   
}

@media screen and (min-width: 768px ) {
    .picContainer{
        flex-direction: row;
    } 
}

.codepenEmbedded{
    padding-top: 2.4rem;
}


